.sidebar {
    background-color: var(--clr-main-background);
    display: flex;
    flex-direction: column;
    padding: 4rem 0;
  }
  
  .sidebar > a:first-child {
    padding: 0;
  }
  .sidebar > a > img {
    /*width: 100%;*/
    display: block;
    max-height:150px;
    transition: all 0.5s;
    margin-left: auto;
    margin-right: auto;
  }
  
  .sidebar > a > img:hover {
    filter: drop-shadow(0 0 0.6vmax var(--clr-sidebarlogo-hover));
  }
  .sidebar a {
    text-decoration: none;
    color: var(--clr-sidebar);
    font: 200 1rem "Roboto";
    padding: 2rem;
    transition: all 0.5s;
  }
  .sidebar a:hover {
    color: var(--clr-main-theme);
    transform: scale(1.1);
  }
  
  .sidebar a > P {
    display: flex;
    align-items: center;
  }
  .sidebar a > p > svg {
    margin-right: 0.5rem;
  }
  
  .MuiTypography-root {
    background-color: var(--clr-main-background) !important;
  }
