.emptyCart {
    margin: auto;
    text-align: center;
    padding: 10vmax;
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .emptyCart > svg {
    font-size: 5vmax;
    color: var(--clr-main-theme);
  }
  .emptyCart > p {
    font-size: 2vmax;
    color:var(--clr-text-primary);
  }
  .emptyCart > a {
    background-color: rgb(51, 51, 51);
    color: var(--clr-text-primary);
    border: none;
    padding: 1vmax 3vmax;
    cursor:  url("../../images/cursor.png"), auto;
    font: 400 1vmax "Roboto";
    text-decoration: none;
  }
  .emptyCart > a > p{
    color: var(--clr-text-primary);
  }
  .cartPage {
    padding: 5vmax;
  }
  
  .cartHeader {
    background-color: var(--clr-main-theme);
    width: 90%;
    box-sizing: border-box;
    margin: auto;
    color: white;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;/*konsa para tag kitna space lega*/
    font: 300 0.7vmax "Roboto";
  }
  .cartHeader > p {
    margin: 10px;
  }
  .cartHeader > p:last-child {
    text-align: end;
  }
  
  .cartContainer {
    width: 90%;
    margin: auto;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
  }
  
  .cartInput {
    display: flex;
    align-items: center;
    height: 8vmax;
  }
  
  .cartInput > button {
    border-radius: 50%;
    border:none;
    height: 2vmax;
    width: 2vmax;
    background-color: var(--clr-svg);
    padding: 0.1vmax;
    cursor:  url("../../images/cursor.png"), auto;
    color: white;
    transition: all 0.5s;
    margin:0.6em;
  }
  .cartInput > button:hover {
    background-color: var(--clr--cartInput-hover);
  }
  
  .cartInput > input {
    border: none;
    padding: 0.5vmax;
    width: 2vmax;
    text-align: center;
    outline: none;
    font: 400 1.4vmax "Roboto";
    color: var(--clr-text-primary);
    background-color: var(--clr-main-background);
  }
  
  .cartSubtotal {
    display: flex;
    padding: 0.5vmax;
    height: 8vmax;
    align-items: center;
    box-sizing: border-box;
    font: 300 1vmax cursive;
    justify-content: flex-end;
    color: var(--clr-text-primary);
  }
  
  .cartGrossProfit {
    display: grid;
    grid-template-columns: 2fr 1.2fr;
  }
  
  .cartGrossProfitBox {
    border-top: 3px solid var(--clr-main-theme);
    margin: 1vmax 4vmax;
    box-sizing: border-box;
    padding: 2vmax 0;
    font: 300 1vmax "Roboto";
    display: flex;
    justify-content: space-between;
    color:var(--clr-text-primary);
  }
  
  .checkOutBtn {
    display: flex;
    justify-content: flex-end;
  }
  .checkOutBtn > button {
    background-color: var(--clr-main-theme);
    color: white;
    border: none;
    padding: 0.8vmax 4vmax;
    width: 50%;
    font: 300 0.8vmax "Roboto";
    margin: 1vmax 4vmax;
    cursor:  url("../../images/cursor.png"), auto;
    border-radius: 30px;
    transition: all 0.5s;
  }

  .checkOutBtn > button:hover{
    background-color: var(--clr-main-hover);
    transform: scale(1.03);
  }
  
  @media screen and (max-width: 600px) {
    .cartPage {
      padding: 0;
      min-height: 60vh;
    }
  
    .cartHeader {
      width: 100%;
      font: 300 1.7vmax "Roboto";
      grid-template-columns: 3fr 1fr 1fr;
    }
  
    .cartContainer {
      width: 100%;
      grid-template-columns: 3fr 1fr 1fr;
    }
  
    .cartInput {
      height: 20vmax;
    }
  
    .cartInput > button {
      padding: 1.5vmax;
    }
  
    .cartInput > input {
      width: 2vmax;
      padding: 1.5vmax;
      font: 400 1.8vmax "Roboto";
    }
  
    .cartSubtotal {
      padding: 1.5vmax;
      height: 20vmax;
      font: 300 2vmax "Roboto";
    }
  
    .cartGrossProfit {
      display: grid;
      grid-template-columns: 0fr 2fr;
    }
  
    .cartGrossProfitBox {
      padding: 2vmax;
      font: 300 2vmax "Roboto";
    }
  
    .checkOutBtn > button {
      padding: 2vmax 4vmax;
      width: 100%;
      font: 300 2vmax "Roboto";
    }
  }