.updateProfileContainer {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--clr-second-background);
    position: fixed;
    top: 0%;
    left: 0;
  }
  
  .updateProfileBox {
    background-color: var(--clr-main-background);
    width: 25vw;
    height: 70vh;
    box-sizing: border-box;
    overflow: hidden;
  }
  
  .updateProfileHeading {
    text-align: center;
    color: var(--clr-text-primary);
    font: 400 1.3vmax "Roboto";
    padding: 1.3vmax;
    border-bottom: var(--clr-input-borders);
    width: 50%;
    margin: auto;
  }
  
  .updateProfileForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    justify-content: space-evenly;
    height: 70%;
    transition: all 0.5s;
  }
  
  .updateProfileForm > div {
    display: flex;
    width: 100%;
    align-items: center;
  }
  
  .updateProfileForm > div > input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: var(--clr-input-borders);
    border-radius: 4px;
    font: 300 0.9vmax cursive;
    outline: none;
    background-color: var(--clr-main-background);
    color:var(--clr-text-primary);
  }
  
  .updateProfileForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: var(--clr-svg);
  }
  
  #updateProfileImage > img {
    width: 3vmax;
    border-radius: 100%;
    margin: 1vmax;
  }
  #updateProfileImage > input {
    display: flex;
    padding: 0%;
  }
  
  #updateProfileImage > input::file-selector-button {
    cursor:  url("../../images/cursor.png"), auto;
    width: 100%;
    z-index: 2;
    height: 5vh;
    border: none;
    margin: 0%;
    font: 400 0.8vmax cursive;
    transition: all 0.5s;
    padding: 0 1vmax;
    color: var(--clr-text-secondary);
    background-color: var(--clr-main-background);
  }
  
  #updateProfileImage > input::file-selector-button:hover {
    background-color: var(--clr-hover-selectFile);
  }
  
  .updateProfileBtn {
    border: none;
    background-color: var(--clr-main-theme);
    color: white;
    font: 300 0.9vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor:  url("../../images/cursor.png"), auto;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: var(--box-shadow);
  }
  
  .updateProfileBtn:hover {
    background-color: var(--clr-main-hover);
  }
  
  @media screen and (max-width: 600px) {
    .updateProfileContainer {
      background-color: white;
    }
    .updateProfileBox {
      width: 100vw;
      height: 95vh;
    }
  
    .updateProfileForm {
      padding: 5vmax;
    }
  
    .updateProfileForm > div > input {
      padding: 2.5vmax 5vmax;
      font: 300 1.7vmax cursive;
    }
  
    .updateProfileForm > div > svg {
      font-size: 2.8vmax;
    }
  
    #updateProfileImage > img {
      width: 8vmax;
      border-radius: 100%;
    }
  
    #updateProfileImage > input::file-selector-button {
      height: 7vh;
      font: 400 1.8vmax cursive;
    }
  
    .updateProfileBtn {
      font: 300 1.9vmax "Roboto";
      padding: 1.8vmax;
    }
  }